body * {
  font-family: Helvetica;
  font-size: 14px;
}
.main_container {
  max-width: 935px;
  margin: auto;
  padding: 0 8px;
  display: flex;
  align-items: center;
  height: 500px;
}
.main_container fieldset {
  border: 0px none;
  padding: 20px;
  width: 100%;
}
.main_container fieldset .language_dropdown_span {
  border: 1px solid #ddd;
  padding: 5px;
}
.main_container fieldset .language_dropdown {
  padding: 5px;
  border: 0px none;
  background: transparent;
  width: 200px;
}
.main_container fieldset .description {
  margin-top: 25px;
}
.main_container fieldset .description textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
}
.main_container fieldset .description description_suggestion {
  display: none;
}
